import React, { ReactElement, ReactNode, useEffect, useRef, useState } from "react";

/** KeepAlive ÁªÑ‰ª∂ */
interface KeepAliveProps {
    activeKey: string; // ÂΩìÂâçÊøÄÊ¥ªÁöÑÁªÑ‰ª∂ Key
    children: ReactElement; // ÈúÄË¶ÅÁºìÂ≠òÁöÑÁªÑ‰ª∂
}

const cache = new Map<string, ReactNode>(); // ÁªÑ‰ª∂ÁºìÂ≠ò

const KeepAlive: React.FC<KeepAliveProps> = ({ activeKey, children }) => {
    const ref = useRef<HTMLDivElement>(null);
    const [, forceUpdate] = useState({});

    useEffect(() => {
        // Á°Æ‰øùÂΩìÂâçÁªÑ‰ª∂Â∑≤ÁªèÁºìÂ≠ò
        if (!cache.has(activeKey)) {
            cache.set(activeKey, children);
        }
        // Âº∫Âà∂Âà∑Êñ∞
        forceUpdate({});
    }, [activeKey, children]);

    return (
        <div ref={ref}>
            {Array.from(cache.entries()).map(([key, component]) => (
                <div
                    key={key}
                    style={{ display: key === activeKey ? "block" : "none" }} // ‰ªÖÂ±ïÁ§∫ÂΩìÂâçÊøÄÊ¥ªÁöÑÁªÑ‰ª∂
                >
                    {component}
                </div>
            ))}
        </div>
    );
};

export default KeepAlive;
